<template>
  <div id="app">
    <nav>
      <a @click='state = "hello"' href="#">Helloページへ</a> |
      <a @click='state = "info"' href="#">情報ページへ</a> |
      <a @click='state = "contact"' href="#">コンタクトページへ</a>
    </nav>
    <img alt="Vue logo" src="./assets/logo.png">
    <main>
      <HelloWorld v-if='state === "hello"' msg="Welcome to Your Vue.js App" />
      <information v-if='state === "info"' />
      <Contact v-if='state === "contact"' />
    </main>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import Information from "./components/Information.vue";
import Contact from "./components/Contact.vue";

export default {
  name: "app",
  data: function() {
    return {
      state: "hello",
      products: [{
        id: 0,
        value: "コーラ",
        price: 120,
        count: 1,
      }, {
        id: 1,
        value: "コーヒー",
        price: 150,
        count: 2
      }, {
        id: 2,
        value: "水",
        price: 100,
        count: 1,
      }],
    };
  },
  components: {
    HelloWorld,
    Information,
    Contact,
  },
  computed: {
    totalPrice() {
      let totalPrice = 0;
      for (const product of this.products) {
        totalPrice += product.price * product.count;
      }
      return totalPrice;
    },
  },
};
</script>

